$almost-black: #0f1112;
$brownish-grey: #666666;
$desert: #ccac5b;
$dark-grey: #22272b;
$cool-grey: #a7a9ac;

$open-sans: 'Open Sans', sans-serif;

@mixin header {
  font-family: $open-sans;
  font-size: 24px;
  font-weight: 600; }
@mixin header_big {
  @include header;
  font-size: 36px;
  letter-spacing: 1.5px; }

@mixin header_sml {
  @include header;
  font-weight: 400;
  text-align: center; }

@mixin header_sml_semibold {
  @include header;
  text-align: center;
  letter-spacing: 18px; }

@mixin basic {
  font-family: $open-sans;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1px; }

@mixin basic_exp {
  @include basic;
  letter-spacing: 15px; } // mia≈Ço byc 9 ale 15 wyglada tak jak na designie

@mixin basic_exp_semibold {
  @include basic;
  font-weight: 600;
  letter-spacing: 9px; }
